export default {
	onLoad(e) {
		console.log(e.id)
		if(e.id == 1){
			this.bindRepairing()
		} else if(e.id == 2){
			this.isFinished = false
		} 
		
		// this.bindRequest(e.id - 1)
		
	},
	data() {
		return {
			isFinished:true,
			isCustom:false,
			isRepairing:false,
			isAleadyTaken: false,
			isOnsite:false,
			phoneType:'苹果 iPhone7 玫瑰金',
			totalPrice:'￥'+ 990,
			repairContent: [{
				title:'屏幕损坏',
				price:'￥499'
			},{
				title:'电池不续航',
				price:'￥499'
			}],
			content:{}
		}
	},
	methods:{
		bindVideo(){
			uni.navigateTo({
				url: '../business-finished/business-finished'
			})
		},
		bindAleadyTaken(){
			this.isAleadyTaken = true
		},
		bindRepairing(){
			this.isAleadyTaken = false
			this.isRepairing = true
		},
		bindOncustom(){
			this.isCustom = true
			this.isRepairing = false
		},
		bindRequest(sid){
			uni.request({
				url:'https://120.24.180.246/xmRepair/order/findShopOrderByStatus',
				method:'POST',
				header:{
					'content-type': 'application/x-www-form-urlencoded'
				},
				data:{
					shopId:1,
					orderStatus1:sid
				},
				success:res=>{
					// this.content = res.data.data
					console.log(res.data.data)
				}
			})
		}
	}
}
